.requisites__title {
  font-weight: inherit;
  font-size: 16px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $soft-black;
}

.requisites__list {
  @include ul-default($padding: 0 0 0 15px);
}

.requisites__item {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 4px;
    left: -15px;

    width: 7px;
    height: 7px;

    background: url("../img/double-right-arrows-angles.svg") no-repeat;
    background-size: contain;
  }
}
