.buyoc-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 10px;

  background-color: $soft-gray;

  @media (min-width: $tablet-width) {
    padding: 20px;
  }

  @media (min-width: $desktop-width) {
    padding: 20px 40px;
  }
}

.buyoc-form__title {
  display: none;

  @media (min-width: $desktop-width) {
    display: inline;
    margin-top: 0;

    font-weight: normal;
    font-size: 20px;
    font-family: $font-stack-o;
    text-align: center;
    text-transform: uppercase;
  }
}

.buyoc-form__sub-title {
  margin: 0 0 20px 0;
  padding: 0 20px;

  text-align: center;

  @media (min-width: $tablet-width) {
    margin: 0;
    margin-bottom: 20px;
    padding: 0;
  }
}

.buyoc-form .buyoc-form__user-field {
  margin: 0;
  padding: 0;

  border: 0;
}

.buyoc-form__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  padding: 0 20px;

  list-style-type: none;

  @media (min-width: $tablet-width) {
    padding: 0;
  }
}

.buyoc-form__item {
  position: relative;

  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 13px;

  &--name,
  &--comment {
    width: 100%;
  }

  &--email,
  &--phone {
    width: 100%;

    svg {
      position: absolute;
      right: 8px;

      fill: $soft-black;
    }

    @media (min-width: $tablet-width) {
      width: 48%;
    }
  }

  &--email:hover svg,
  &--phone:hover svg {
    fill: $black;
  }

  &--email:active svg,
  &--phone:active svg {
    fill: $green;
  }
}

.buyoc-form__item input {
  position: relative;

  width: 100%;
  height: 34px;
  padding: 7px 16px;
  padding-right: 40px;

  font-size: 12px;
  line-height: inherit;
  font-family: inherit;
  color: inherit;

  border: 0;
  outline: none;
  box-shadow: 0 0 0 1px $soft-gray;

  &:hover {
    box-shadow: 0 0 0 1px $border-gray;
  }

  &:active,
  &:focus {
    box-shadow: 0 0 0 2px $soft-black;
  }

  &:active ~ svg,
  &:focus ~ svg {
    fill: $green;
  }

  &:invalid:not(:placeholder-shown),
  &:focus:invalid {
    box-shadow: 0 0 0 2px $error;
  }

  &:invalid:not(:placeholder-shown) ~ svg,
  &:focus:invalid ~ svg {
    fill: $error;
  }
}

.buyoc-form__item input::placeholder {
  color: $soft-black;
}

.buyoc-form__comment {
  display: block;
  width: 100%;
  height: 70px;
  margin: 0 auto;
  padding: 13px 0 9px 14px;

  font-size: 12px;
  line-height: inherit;
  font-family: inherit;
  color: inherit;

  border: 0;
  outline: none;
  box-shadow: 0 0 0 1px $soft-gray;

  &:hover {
    box-shadow: 0 0 0 1px $border-gray;
  }

  &:active,
  &:focus {
    box-shadow: 0 0 0 2px $soft-black;
  }
}

.buyoc-form__legend {
  text-align: left;
}

.buyoc-form__button-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 10px 20px;

  @media (min-width: $tablet-width) {
    margin: 10px 0;
  }
}

.buyoc-form__button {
  width: 100%;
  margin-top: 21px;
  padding: 10px;

  font-size: 16px;
  line-height: 20px;

  border: none;
  outline: none;
  cursor: pointer;
}

.buyoc-form__sub-description {
  margin: 10px 20px;

  font-weight: normal;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
  text-align: center;
  color: $soft-black;

  &--color,
  &--color a {
    color: $main-color;
  }

  &--color a {
    font-style: italic;

    &:hover {
      color: $btn-hover;
    }

    &:active,
    &:focus {
      color: $btn-pressed;
    }
  }

  @media (min-width: $tablet-width) {
    margin: 10px 0;
  }
}
