.reviews {
  margin-top: 40px;
}

.reviews__title {
  position: relative;

  margin-left: 25px;

  font-weight: inherit;
  font-size: 14px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $soft-black;
  text-transform: uppercase;

  &::after {
    content: "";
    position: absolute;
    top: 5px;
    left: -25px;

    width: 14px;
    height: 14px;

    background: url("../img/icon-comments.svg") no-repeat;
    background-size: contain;
  }

  &::before {
    right: 0;

    width: 590px;
  }
}

.reviews__comment {
  position: relative;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 20px 0 40px;
  padding: 20px;
  padding-left: 80px;

  border: 1px solid $border-gray;

  &::before {
    content: "";
    position: absolute;
    left: 10px;

    width: 48px;
    height: 48px;

    background: url("../img/icon-user.svg") no-repeat;
    background-position-y: 50%;
    background-size: contain;
    border: 1px solid $border-gray;
    border-radius: 50%;
  }
}

.reviews__comment-name {
  margin: 0 auto 20px 0;

  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: inherit;
}

.reviews__comment-date {
  margin: 0 20px 0 0;

  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: $font-stack-r;
  color: inherit;
}

.reviews__comment-body {
  width: 100%;

  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: $font-stack-r;
  color: inherit;
}
