.main-nav {
  position: relative;
  z-index: 1;

  width: auto;
  min-height: 0;

  background-color: $soft-black;

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 50px;
    left: 0;

    width: 100%;
    height: 80px;
    padding: 0 40px;

    background-color: $white;
    border-top: 1px solid $black;
  }

  @media (min-width: $desktop-width) {
    top: 80px;

    padding: 0;

    &--is-fixed {
      position: fixed;
      top: 0;

      box-shadow: 0 2px 6px transparentize($black, 0.75);
    }
  }
}

.main-nav__toggle {
  z-index: 2;

  display: block;
  width: 24px;
  height: 24px;
  padding: 0;

  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;

  transition: 0.3s;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;

  width: 24px;
  height: 3.5px;

  background-color: $white;
  border-radius: 4px;
  box-shadow: 0 10px 0 0 $white, 0 20px 0 0 $white;
  transform-origin: 50% 50%;

  transition: 0.3s 0.3s, transform 0.3s;
}

.main-nav--closed .main-nav__list {
  @media (max-width: $mobile-width-only) {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 14px;
    left: 0;

    width: 24px;
    height: 2px;

    background-color: $white;

    transition: 0.3s, transform 0.3s 0.3s;
  }

  &::before {
    transform: rotate3d(0, 0, 1, 45deg);
  }

  &::after {
    transform: rotate3d(0, 0, 1, -45deg);
  }
}

.main-nav--nojs .main-nav__toggle {
  @media (max-width: $mobile-width-only) {
    display: none;
  }
}

.main-nav--opened .main-nav__wrap {
  position: absolute;
  top: 41px;
  right: -20px;

  width: 240px;
  min-height: 500px;

  background-color: $soft-black;
  border-bottom: 1px solid $black;
  border-left: 1px solid $black;
}

.main-nav--closed .main-nav__wrap {
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    margin: 0 auto;
    padding: 1px 40px;
  }
}

.main-nav__list {
  @include ul-default($padding: 20px 20px 10px 50px);

  @media (min-width: $tablet-width) {
    display: flex;
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    flex-grow: 1;
  }
}
