.button {
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  font-family: $font-stack-o;
  text-align: center;
  color: $white;
  text-transform: uppercase;

  background-color: $main-color;
  border: none;

  &:hover {
    background-color: $btn-hover;
  }

  &:active,
  &:focus {
    background-color: $btn-pressed;
  }
}
