.pagination__list {
  display: flex;
  flex-wrap: wrap;

  @include ul-default;
}

.pagination__item {
  padding: 10px;

  font-weight: inherit;
  font-size: inherit;
  font-family: $font-stack-r;
  text-align: center;
  color: $soft-black;

  border: 1px solid $border-gray;
  cursor: pointer;

  &:not(:first-child):not(:last-child) {
    width: 38px;
  }

  &:hover {
    background-color: $soft-gray;
  }

  &--active,
  &--active:hover {
    color: $white;

    background-color: $main-color;
    border-color: $main-color;
  }
}
