.page-information {
  border-top: 1px solid $border-gray;
}

.page-information__contacts {
  border-top: 1px solid $border-gray;
}

.page-information__list {
  width: 200px;

  @include ul-default($margin: 10px 0 0 0);
}

.page-information__item-title {
  font-weight: inherit;
  font-size: 16px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $soft-black;
}

.page-information__item-button {
  position: relative;

  display: block;
  width: 200px;
  padding: 10px 0 10px 20px;

  text-align: left;
  color: $soft-black;
  text-transform: none;

  background: none;

  &::after {
    content: "";
    position: absolute;
    top: calc(50% - 4px);
    left: 0;

    width: 5px;
    height: 8px;

    background: url("../img/left_point.svg");
    transform: rotate(180deg);
  }

  &:hover {
    color: $btn-hover;

    background: none;
  }

  &:active,
  &:focus {
    color: $btn-pressed;

    background: none;
  }
}
