.authorization-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.authorization-form__title {
  font-weight: bold;
  font-size: 14px;
  font-family: $font-stack-o;
  color: $soft-black;

  @include h-default($margin: 0 0 20px 0);
}

.authorization-form .authorization-form__user-field {
  margin: 0;
  padding: 0;

  border: 0;
}

.authorization-form__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include ul-default;
}

.authorization-form__item {
  position: relative;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 13px;

  &--name,
  &--comment {
    width: 100%;
  }

  &--email,
  &--phone {
    svg {
      position: absolute;
      top: 8px;
      right: 8px;

      fill: $soft-black;
    }
  }

  &--email:hover svg,
  &--phone:hover svg {
    fill: $black;
  }

  &--email:active svg,
  &--phone:active svg {
    fill: $green;
  }
}

.authorization-form__item label {
  font-weight: bold;
  font-size: inherit;
  font-family: inherit;
  color: inherit;
}

.authorization-form__sub-text {
  width: 190px;
  margin-left: auto;

  font-size: 12px;
  color: $sub-text;

  @media (min-width: $tablet-width) {
    width: 210px;
  }

  @media (min-width: $desktop-width) {
    width: 400px;
  }
}

.authorization-form__item input {
  position: relative;

  width: 190px;
  height: 34px;
  padding: 7px 16px;
  padding-right: 40px;

  font-size: 12px;
  line-height: inherit;
  font-family: inherit;
  color: inherit;

  border: 0;
  outline: none;
  box-shadow: 0 0 0 1px $border-gray;

  &:hover {
    box-shadow: 0 0 0 1px $border-gray-dark;
  }

  &:active,
  &:focus {
    box-shadow: 0 0 0 2px $soft-black;
  }

  &:active ~ svg,
  &:focus ~ svg {
    fill: $green;
  }

  &:invalid:not(:placeholder-shown),
  &:focus:invalid {
    box-shadow: 0 0 0 2px $error;
  }

  &:invalid:not(:placeholder-shown) ~ svg,
  &:focus:invalid ~ svg {
    fill: $error;
  }

  @media (min-width: $tablet-width) {
    width: 210px;
  }

  @media (min-width: $desktop-width) {
    width: 400px;
  }
}

.authorization-form__item input::placeholder {
  color: $soft-black;
}

.authorization-form__legend {
  text-align: left;
}

.authorization-form__button {
  width: 150px;
  margin-right: 40px;
  margin-left: auto;
  padding: 10px;

  outline: none;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    margin-right: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 250px;
  }
}
