.article-product-l {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 20px;

  background-color: $white;
  border: 1px solid $border-gray;
  border-top-width: 3px;
  border-top-color: $main-color;

  @media (min-width: $tablet-width) {
    padding: 30px;
  }
}

.article-product-l__image {
  width: 150px;
}

.article-product-l__description {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 350px;
  margin: 0 auto;
}

.article-product-l__sub-title {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;

  border-bottom: 1px solid $border-gray;
}

.article-product-l__intro ul {
  @include ul-default($margin: 10px 0 0 0);
}

.article-product-l__rate {
  margin: 9px 0 6px;

  cursor: pointer;
}

.article-product-l__title {
  margin: 0;

  font-weight: normal;
  font-size: 16px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $soft-black;
}

.article-product-l__prices {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: baseline;
  min-height: 46px;
}

.article-product-l__price {
  margin: 0;

  font-style: italic;
  font-size: 24px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $green;

  &--currency {
    font-style: normal;
    font-size: 16px;
    color: $soft-black;
  }
}

.article-product-l__old-price {
  margin: 0;

  font-style: italic;
  font-size: 12px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $soft-black;

  text-decoration-color: $error;
  text-decoration-line: line-through;
  text-decoration-style: inherit;

  &--currency {
    font-size: 12px;
    color: $soft-black;
  }
}

.article-product-l__link .article-product-l__title:hover {
  color: $main-color;
}

.article-product-l__link .article-product-l__title:active,
.article-product-l__link .article-product-l__title:focus {
  opacity: 0.5;
}

.article-product-l__link {
  display: inline-block;
}

.article-product-l__buttons {
  position: relative;

  display: flex;
  flex-direction: column;
  width: 200px;
}

.article-product-l__buyoneclick,
.article-product-l__cart {
  width: 100%;
  padding: 10px 0;

  outline: none;
  cursor: pointer;
}

.button.article-product-l__buyoneclick {
  color: $soft-black;

  background-color: $soft-gray;

  &:hover {
    color: $main-color;

    background-color: $soft-gray;
  }

  &:active,
  &:focus {
    color: $btn-pressed;

    background-color: $soft-gray;
  }
}

.article-product-l__cart {
  margin-top: 10px;
  margin-bottom: 10px;
}

.article-product-l__compare,
.article-product-l__favorite {
  height: 14px;
  margin: auto 0;
  margin-left: 10px;
  padding: 0;

  background: none;
  border: 0;
  outline: none;
  cursor: pointer;
}

.article-product-l__compare svg,
.article-product-l__favorite svg {
  fill: $soft-black;
}

.article-product-l__compare:hover svg,
.article-product-l__favorite:hover svg {
  fill: $main-color;
}

.article-product-l__compare:active svg,
.article-product-l__compare:focus svg,
.article-product-l__favorite:active svg,
.article-product-l__favorite:focus svg {
  fill: $btn-pressed;
}
