.categories-tiles-list {
  flex-direction: row;
  flex-wrap: wrap;

  @include ul-default($padding: 0 20px 15px);

  @media (min-width: $tablet-width) {
    @include ul-default($padding: 0 40px 15px);
  }

  @media (min-width: $desktop-width) {
    @include ul-default();
  }
}

.categories-tiles-list__item {
  width: 46.43%;
  margin-bottom: 25px;

  @media (max-width: $mobile-width-only) {
    &:not(:last-child):nth-child(2n + 1) {
      margin-right: auto;
    }
  }

  @media (min-width: $tablet-width) {
    width: 21.8%;
    min-height: 200px;

    &:not(:last-child):not(:nth-child(4n + 4)) {
      margin-right: 30px;
    }
  }

  @media (min-width: $desktop-width) {
    width: 160px;
    min-height: 175px;

    &:not(:last-child):not(:nth-child(4n + 4)) {
      margin-right: auto;
    }

    &:not(:last-child):not(:nth-child(6n + 6)) {
      margin-right: 32px;
    }
  }
}

.categories-tiles-list__link {
  position: relative;

  display: flex;
  flex-direction: column;

  &:hover h2 {
    color: $btn-hover;
  }

  &:active h2,
  &:focus h2 {
    color: $btn-pressed;
  }
}

.categories-tiles-list__title {
  order: 2;

  font-weight: inherit;
  font-size: 14px;
  line-height: 16px;
  font-family: $font-stack-o;
  text-align: center;
  color: $soft-black;
}

.categories-tiles-list__image-box {
  order: 1;
  padding: 20px;
  padding-bottom: 12.31%;

  text-align: center;

  background-color: $soft-gray;
}
