.page-footer__top {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  background-color: $soft-black;

  &::before {
    content: "";
    position: absolute;

    width: 100%;

    border-top: 1px solid $black;
  }

  &::after {
    content: "";
    position: absolute;

    width: 100%;

    border-bottom: 1px solid $black;
  }

  @media (min-width: $tablet-width) {
    &::before,
    &::after {
      display: none;
    }
  }
}

.page-footer__top-intro {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 20px;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0;
    padding: 0 40px;

    background-color: $white;
  }

  @media (min-width: $desktop-width) {
    position: relative;

    width: 1200px;
    margin: 0 auto;

    background: none;

    &::before {
      content: "";
      position: absolute;
      bottom: -1px;

      width: 1120px;

      border-bottom: 1px solid $gray;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 0;

      width: 1120px;

      border-bottom: 1px solid $black;
    }
  }
}

.page-footer__adresses {
  margin: 0 20px;
  padding-bottom: 38px;

  @media (min-width: $tablet-width) {
    display: flex;
    margin: 0 40px;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    margin: 0 auto;
    margin-bottom: 20px;
    padding: 0 40px;
  }
}

.page-footer__small-description {
  position: relative;

  margin: 0 0 20px 0;
  padding-bottom: 20px;

  font-weight: normal;
  color: $white;

  border-bottom: 1px solid $black;

  &::after {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;

    width: 100%;

    border-bottom: 1px solid $gray;
  }

  @media (min-width: $tablet-width) {
    display: none;
  }

  @media (min-width: $desktop-width) {
    display: block;
    order: 2;
    width: 270px;
    margin: 0;
    margin-top: 34px;
    padding: 0;

    border: none;

    &::after {
      display: none;
    }
  }
}

.page-footer__logo {
  display: inline-block;
  margin: 15px 0;

  @media (min-width: $tablet-width) {
    order: 1;
    height: 24px;
    margin-top: 32px;
    margin-bottom: 36px;
  }

  @media (min-width: $desktop-width) {
    height: 48px;
    margin-top: 34px;
    margin-bottom: 18px;
  }
}

.page-footer__description {
  display: none;

  @media (min-width: $tablet-width) {
    position: relative;

    display: flex;
    justify-content: space-between;
    margin: 0 40px;
    padding-top: 34px;
    padding-bottom: 18px;

    border-top: 1px solid $black;
    border-bottom: 1px solid $black;

    &::after {
      content: "";
      position: absolute;
      bottom: -2px;
      left: 0;

      width: 100%;

      border-bottom: 1px solid $gray;
    }
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.page-footer__description .page-footer__small-description {
  @media (min-width: $tablet-width) {
    display: block;
    width: 270px;
    margin: 0;
    padding: 0;

    border: none;

    &::after {
      display: none;
    }
  }
}

.page-footer__main-contacts {
  @media (min-width: $tablet-width) {
    order: 2;
    margin-top: 30px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 34px;
  }
}

.page-footer__social {
  position: relative;

  margin-top: 20px;
  padding-bottom: 18px;

  border-bottom: 1px solid $black;

  @media (min-width: $tablet-width) {
    margin-top: 34px;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;

    width: 100%;

    border-bottom: 1px solid $gray;
  }

  @media (min-width: $tablet-width) {
    order: 3;

    border: none;
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.page-footer__phone,
.page-footer__mail {
  font-weight: normal;
  color: $main-color;

  &--number,
  &--email {
    font-style: italic;
    color: $white;

    border-bottom: 1px dotted white;

    &:hover {
      border: none;
      opacity: 0.8;
    }
  }

  @media (min-width: $tablet-width) {
    color: $soft-black;

    &--number,
    &--email {
      color: $main-color;

      border: none;

      &:hover {
        color: $btn-hover;

        opacity: 1;
      }

      &:active,
      &:focus {
        color: $btn-pressed;
      }
    }
  }

  @media (min-width: $desktop-width) {
    color: $white;
  }
}

.page-footer__bottom {
  padding: 20px 0;

  background-color: $main-color;
}

.page-footer__bottom-wrap {
  @media (min-width: $desktop-width) {
    width: 1200px;
    margin: 0 auto;
    padding: 0 40px;
  }
}

.page-footer__bottom p {
  margin: 0 20px;

  color: $white;

  @media (min-width: $tablet-width) {
    margin: 0 40px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.page-footer__bottom p:first-child {
  padding-bottom: 10px;
}

.page-footer__site-map {
  display: none;
  order: 5;
  width: 270px;

  @media (min-width: $desktop-width) {
    display: block;

    text-align: right;
  }
}

.page-footer__site-map-link {
  display: none;

  @media (min-width: $tablet-width) {
    position: absolute;
    right: -40px;

    display: inline-block;
    height: 34px;
    padding: 5px 74px 0 35px;
  }

  @media (min-width: $desktop-width) {
    position: relative;

    margin-top: 34px;
    padding-left: 40px;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 100%;

      width: calc((100vw - 1217px) * 0.5);
      height: 34px;

      background-color: $main-color;
    }
  }
}

.page-footer__site-map-link:hover::after {
  background-color: $btn-hover;
}

.page-footer__site-map-link:active::after,
.page-footer__site-map-link:focus::after {
  background-color: $btn-pressed;
}
