.user-list {
  font-size: 12px;
  font-family: inherit;
  text-align: right;
}

.user-list__item,
.user-list__item a {
  font-family: $font-stack-o;
  color: $white;

  @media (min-width: $desktop-width) {
    font-size: 14px;
  }
}

.user-list__item a {
  border-bottom: 1px dotted $white;

  &:hover {
    border: none;
  }

  &:active,
  &:focus {
    border: none;
    opacity: 0.8;
  }
}
