.ribbons {
  position: relative;
}

.ribbons__ribbon {
  position: absolute;
  top: 0;
  left: -26px;

  width: 70px;
  height: 30px;

  font-weight: normal;
  font-size: 14px;
  line-height: 26px;
  font-family: $font-stack-r;
  text-align: center;
  color: $white;

  &--popular {
    background-image: url("../img/popular-prod.svg");
  }

  @media (min-width: $tablet-width) {
    left: -36px;
  }
}

.ribbons__ribbon span {
  position: absolute;
  top: -27px;
  right: -14px;
}
