.order {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 40px auto;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0 40px;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    margin: 40px auto 80px;
  }
}
