.categories-list {
  @include ul-default($margin: 50px 0 0 0, $padding: 0 20px 15px);

  @media (min-width: $tablet-width) {
    @include ul-default($margin: 25px 0 0 0, $padding: 0 40px 15px);
  }

  @media (min-width: $desktop-width) {
    @include ul-default($margin: 25px 0 0 0);
  }
}

.categories-list__item {
  position: relative;

  display: flex;
  flex-direction: column;
  margin-bottom: 25px;
  padding: 20px;

  background-color: $soft-gray;

  @media (min-width: $tablet-width) {
    width: 48.5%;
    min-height: 200px;
  }

  @media (min-width: $desktop-width) {
    width: 350px;
    min-height: 175px;
  }
}

.categories-list__item:nth-child(2n + 1) {
  @media (min-width: $tablet-width) {
    margin-right: auto;
  }
}

.categories-list__item:nth-child(n + 5) {
  @media (max-width: $tablet-width-only) {
    display: none;
  }
}

.categories-list__title {
  order: 2;
  width: 130px;
  margin: 0 0 14px auto;

  font-weight: bold;
  font-size: 22px;
  line-height: 21px;
  font-family: $font-stack-o;
  text-align: right;
  color: $main-color;

  @media (min-width: $tablet-width) {
    order: 1;
    margin: 0 auto 14px 0;

    text-align: left;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
  }
}

.categories-list__subtitle {
  order: 3;
  width: 130px;
  margin: 0 0 20px auto;

  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  font-family: $font-stack-o;
  text-align: right;
  color: $soft-black;

  @media (min-width: $tablet-width) {
    order: 2;
    width: 170px;
    margin: 0 auto 20px 0;

    text-align: left;
  }

  @media (min-width: $desktop-width) {
    width: 180px;
  }
}

.categories-list__image {
  position: absolute;
  bottom: 97px;

  width: 110px;

  @media (max-width: $mobile-width-only) {
    left: 20px;
  }

  @media (min-width: $tablet-width) {
    right: 20px;

    width: 110px;
  }
}

.categories-list__button {
  position: relative;

  display: block;
  order: 5;
  width: auto;
  margin: 20px 0 0;
  padding: 5px 0;

  &::after {
    content: "";
    position: absolute;
    top: 13px;

    width: 12px;
    height: 7px;
    margin-left: 10px;

    background: url("../img/right-arrow.svg");
    background-repeat: no-repeat;
    background-size: 100%;
  }
}
