.page-not-found {
  border-top: 1px solid $border-gray;
  border-bottom: 1px solid $border-gray;
}

.page-not-found__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 20px auto;
  padding: 0 20px;

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    width: 1200px;
    margin: 35px auto;
    padding: 0 40px;
  }
}

.page-not-found__description {
  order: 2;

  text-align: center;

  @media (min-width: $desktop-width) {
    order: 1;
    width: 350px;

    text-align: left;
  }
}

.page-not-found__image-block {
  order: 1;

  @media (min-width: $desktop-width) {
    order: 2;
    width: 540px;
  }
}

.page-not-found__title {
  margin: 20px 0 20px 0;

  font-weight: 700;
  font-size: 80px;
  line-height: 120px;
  font-family: $font-stack-o;
  color: $soft-black;

  @media (min-width: $desktop-width) {
    margin: 20px 0 30px 0;
  }
}

.page-not-found__sub-title {
  margin: 30px 0 40px 0;

  font-weight: 700;
  font-size: 30px;
  line-height: 44px;
  font-family: $font-stack-o;
  color: $soft-black;
  text-transform: inherit;
}

.page-not-found__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include ul-default;
}

.page-not-found__link {
  font-weight: inherit;
  font-size: 14px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $main-color;
  text-transform: uppercase;

  &:hover {
    color: $btn-hover;
  }

  &:active,
  &:focus {
    color: $btn-pressed;
  }
}
