.section-wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 20px auto;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    padding: 0 40px;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
    width: 1200px;
    margin: 20px auto 60px;
  }
}

.section-wrap__articles {
  @media (min-width: $desktop-width) {
    width: 540px;

    &--article {
      width: 350px;
    }
  }
}

.section-wrap__aside {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (min-width: $desktop-width) {
    justify-content: flex-start;
    width: 350px;

    &--article {
      width: 740px;
    }
  }
}

.section-wrap__list {
  @include ul-default;
}

.section-wrap__item:not(:first-child) {
  margin-top: 40px;
}

.section-wrap__item:last-child {
  margin-bottom: 40px;
}

.section-wrap__item-title,
.section-wrap__aside-title {
  font-weight: inherit;
  font-size: 16px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $soft-black;
}

.section-wrap__sub-list {
  @include ul-default;
}

.section-wrap__sub-item:not(:first-child) {
  margin-top: 5px;
}

.section-wrap__sub-item:last-child {
  margin-bottom: 5px;
}

.section-wrap__link {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: $font-stack-r;
  color: $soft-black;

  &--phone {
    color: $main-color;
  }
}

.section-wrap__link--phone:hover,
.section-wrap__link--mail:hover {
  color: $btn-hover;
}

.section-wrap__link--phone:active,
.section-wrap__link--mail:active,
.section-wrap__link--phone:focus,
.section-wrap__link--mail:focus {
  color: $btn-pressed;
}

.section-wrap__button {
  position: relative;

  display: block;
  width: 280px;
  margin: 20px 0 0;
  padding: 5px 0;

  &::after {
    content: "";
    position: absolute;
    top: 13px;

    width: 12px;
    height: 7px;
    margin-left: 10px;

    background: url("../img/right-arrow.svg");
    background-repeat: no-repeat;
    background-size: 100%;
  }
}

.section-wrap__pagination {
  margin: 40px auto 0;
}

.section-wrap__aside-map iframe {
  width: 100%;

  border: none;
}
