.blog {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 20px auto;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    padding: 0 40px;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
    width: 1200px;
    margin: 20px auto;
  }
}

.blog__blog-articles {
  @media (min-width: $desktop-width) {
    width: 540px;

    &--article {
      width: 730px;
    }

    &--special {
      width: 1200px;
    }
  }
}

.blog__description h2 {
  font-size: 16px;
  line-height: 24px;
}

.blog__description ul {
  @include ul-default($padding: 0 0 0 15px);
}

.blog__description .blog__special-list {
  padding: 0;
}

.blog__description li {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 4px;
    left: -15px;

    width: 7px;
    height: 7px;

    background: url("../img/double-right-arrows-angles.svg") no-repeat;
    background-size: contain;
  }
}

.blog__form {
  margin: 0 0 0 auto;

  @media (min-width: $tablet-width) {
    width: 450px;
  }
}

.blog__blog-list {
  @include ul-default;
}

.blog__blog-item:not(:first-child) {
  margin-top: 40px;
}

.blog__blog-item:last-child {
  margin-bottom: 40px;
}

.blog__title {
  font-weight: inherit;
  font-size: 16px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $soft-black;
}

.blog__item-description {
  @include h-default($margin: 20px 0);
}

.blog__item-data {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.blog__button {
  position: relative;

  display: block;
  width: 280px;
  margin: 20px 0 0;
  padding: 5px 0;

  &::after {
    content: "";
    position: absolute;
    top: 13px;

    width: 12px;
    height: 7px;
    margin-left: 10px;

    background: url("../img/right-arrow.svg");
    background-repeat: no-repeat;
    background-size: 100%;
  }
}

.blog__pagination {
  margin: 40px auto 0;
}

.blog__blog-tags {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (min-width: $desktop-width) {
    justify-content: flex-start;
    width: 350px;
  }
}
