.filter {
  margin-bottom: 30px;
  padding: 20px;

  background-color: $white;
  border: 1px solid $border-gray;
  border-top-width: 3px;
  border-top-color: $main-color;
  box-shadow: 0 2px 6px transparentize($black, 0.75);
}

.filter__title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: bold;
  font-size: 24px;
  font-family: $font-stack-o;
  text-align: center;
  color: inherit;
}
