.single-product-tab {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 40px;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    padding: 0 40px;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    margin: 40px auto;
  }
}

.single-product-tab__description {
  @media (min-width: $desktop-width) {
    width: 730px;
  }
}

.single-product-tab__aside-desc {
  margin-top: 20px;

  @media (min-width: $desktop-width) {
    width: 350px;
    margin-top: 0;
  }
}

.single-product-tab__nav-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;

  border-bottom: 1px solid $border-gray;

  @include ul-default;
}

.single-product-tab__nav-item {
  margin-bottom: -1px;
  padding: 15px 0;

  border: 1px solid;
  border-color: $border-gray;
  border-right-color: transparent;
  border-bottom-color: transparent;

  &:last-of-type {
    border-right-color: $border-gray;
  }

  &--active {
    border-top-color: $border-gray;
    border-bottom-color: $white;
  }
}

.single-product-tab__nav-item:first-child.single-product-tab__nav-item--active {
  border-right-color: transparent;
  border-left-color: $border-gray;
}

.single-product-tab__nav-item:not(:first-child):not(:last-of-type).single-product-tab__nav-item--active {
  border-right-color: transparent;
}

.single-product-tab__nav-item:last-of-type.single-product-tab__nav-item--active {
  border-right-color: $border-gray;
}

.single-product-tab__nav-item a {
  padding: 15px 30px;

  font-weight: inherit;
  font-size: 14px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $soft-black;
  text-transform: uppercase;

  background-color: $soft-gray;

  &:hover {
    background-color: $white;
  }
}

.single-product-tab__nav-item--active a {
  color: $main-color;

  background-color: $white;
}

.single-product-tab__content {
  padding: 20px;

  border: 1px solid $border-gray;
  border-top: none;
}

.single-product-tab__pane {
  display: none;

  &--active {
    display: block;
  }
}

.single-product-tab__aside {
  display: none;

  @media (min-width: $desktop-width) {
    position: relative;

    display: block;
    width: 392px;
    height: 490px;
    margin-bottom: 28px;
    padding: 20px;

    background-color: $white;

    &::after {
      content: "";
      position: absolute;
      top: -1px;
      left: -2px;
      z-index: -1;

      width: 399px;
      height: 496px;

      background-color: $main-color;
    }
  }
}

.single-product-tab__aside-list {
  @include ul-default;
}

.single-product-tab__aside-item {
  position: relative;

  padding-left: 49px;
}

.single-product-tab__aside-item svg {
  position: absolute;
  left: 0;

  width: 28px;
  height: 25px;

  fill: $main-color;
}

.single-product-tab__aside-title {
  font-size: 16px;

  @media (min-width: $desktop-width) {
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    font-family: $font-stack-o;
  }
}

.single-product-tab__form {
  margin: 0 0 0 auto;

  @media (min-width: $tablet-width) {
    width: 450px;
  }
}
