.contacts {
  @media (min-width: $tablet-width) {
    position: relative;

    border-top: 1px solid $border-gray;
  }
}

.contacts__map {
  display: none;
  height: 480px;

  @media (min-width: $tablet-width) {
    display: block;
  }
}

.contacts__map iframe {
  width: 100%;

  border: none;
}

.contacts__title {
  position: relative;

  width: 175px;
  margin: 20px auto;

  font-weight: normal;
  font-size: 20px;
  line-height: 30px;
  font-family: $font-stack-o;
  text-align: center;
  color: $soft-black;
  text-transform: uppercase;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;

    width: 40px;
    height: 1px;

    background-color: $border-gray;
  }

  &::before {
    left: -51px;
  }

  &::after {
    left: 187px;
  }

  @media (min-width: $tablet-width) {
    &::before,
    &::after {
      width: 70px;
    }

    &::before {
      left: -89px;
    }

    &::after {
      left: 194px;
    }
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.contacts__form-wrap {
  @media (min-width: $desktop-width) {
    position: relative;

    width: 1200px;
    margin: 0 auto;
  }
}

.contacts__form {
  margin: 0 0 20px 0;

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 94px;
    right: 40px;

    width: 330px;
    padding: 25px;

    background-color: white;
  }

  @media (min-width: $desktop-width) {
    top: -455px;

    width: 540px;
    height: 430px;
  }
}
