.list-title {
  @media (min-width: $tablet-width) {
    position: relative;

    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    font-family: $font-stack-o;
    color: $soft-black;
    text-transform: uppercase;

    &::before {
      content: "";
      position: absolute;
      top: 50%;

      height: 1px;

      background-color: $border-gray;
    }
  }
}
