.information-box {
  padding: 15px 18px;

  background-color: $soft-gray;
  border-left: 3px solid $main-color;
}

.information-box__description,
.information-box__update-date {
  @include h-default;
}

.information-box__description {
  margin-bottom: 15px;
}

.information-box__update-date {
  margin-top: 15px;

  font-weight: inherit;
  font-size: 10px;
  line-height: 12px;
  font-family: inherit;
  text-align: right;
  color: $sub-text;
}
