.article-partner {
  height: 110px;

  background-color: $white;
  border: 1px solid $border-gray;
  border-top-width: 3px;
  border-top-color: $main-color;
}

.article-partner__image {
  position: relative;
  top: 50%;
  left: 50%;

  transform: translateX(-50%) translateY(-50%);
}

.article-partner:hover {
  box-shadow: 0 8px 16px transparentize($black, 0.75);
}

.article-partner:active,
.article-partner:focus {
  box-shadow: 0 8px 16px transparentize($black, 0.75);
  opacity: 0.25;
}
