.blog-tags__list {
  @include ul-default($padding: 0 0 0 15px);
}

.blog-tags__item {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: calc(100% / 2);
    left: -15px;

    width: 7px;
    height: 7px;

    background: url("../img/double-right-arrows-angles.svg") no-repeat;
    background-size: contain;
  }
}

.blog-tags__title {
  font-weight: inherit;
  font-size: 16px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $soft-black;
}

.blog-tags__link {
  font-weight: inherit;
  font-size: 14px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $soft-black;
  text-transform: inherit;

  &:hover {
    color: $btn-hover;
  }

  &:active,
  &:focus {
    color: $btn-pressed;
  }
}
