.article-product-g {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;

  background-color: $white;
  border: 1px solid $border-gray;
  border-top-width: 3px;
  border-top-color: $main-color;

  @media (min-width: $tablet-width) {
    padding: 30px;
  }
}

.article-product-g:hover {
  box-shadow: 0 2px 6px transparentize($black, 0.75);
}

.article-product-g:active,
.article-product-g:focus {
  box-shadow: 0 2px 6px transparentize($black, 0.75);
  opacity: 0.25;
}

.article-product-g__image {
  width: 100%;
}

.article-product-g__description {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  border-top: 1px solid $border-gray;
}

.article-product-g__rate {
  margin: 9px 0 6px;

  cursor: pointer;
}

.article-product-g__title {
  margin: 0;

  font-weight: normal;
  font-size: 16px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $soft-black;
}

.article-product-g__prices {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: baseline;
  min-height: 46px;
  margin-top: 25px;
}

.article-product-g__price {
  margin: 0;

  font-style: italic;
  font-size: 24px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $green;

  &--currency {
    font-style: normal;
    font-size: 16px;
    color: $soft-black;
  }
}

.article-product-g__old-price {
  margin: 0;

  font-style: italic;
  font-size: 12px;
  line-height: 21px;
  font-family: $font-stack-o;
  color: $soft-black;

  text-decoration-color: $error;
  text-decoration-line: line-through;
  text-decoration-style: inherit;

  &--currency {
    font-size: 12px;
    color: $soft-black;
  }
}

.article-product-g__link .article-product-g__title:hover {
  color: $main-color;
}

.article-product-g__link .article-product-g__title:active,
.article-product-g__link .article-product-g__title:focus {
  opacity: 0.5;
}

.article-product-g__link {
  display: inline-block;
}

.article-product-g__buttons {
  position: relative;
}

.article-product-g__buyoneclick,
.article-product-g__cart {
  position: absolute;
  bottom: 1px;

  padding: 0;

  background: none;
  border: 0;
  outline: none;
  cursor: pointer;

  &:active,
  &:focus {
    fill: $btn-pressed;
  }
}

.article-product-g__buyoneclick {
  right: 60px;

  width: 26px;
  height: 34px;
}

.article-product-g__cart {
  right: 0;

  width: 40px;
  height: 34px;
}

.article-product-g__compare,
.article-product-g__favorite {
  position: absolute;
  right: 2px;

  width: 24px;
  height: 22px;
  padding: 0;

  background: none;
  border: 0;
  outline: none;
  cursor: pointer;
}

.article-product-g__compare {
  top: -340px;

  @media (min-width: $tablet-width) {
    top: -344px;
  }

  @media (min-width: $desktop-width) {
    top: -304px;
  }
}

.article-product-g__favorite {
  top: -380px;

  @media (min-width: $tablet-width) {
    top: -384px;
  }

  @media (min-width: $desktop-width) {
    top: -344px;
  }
}

.article-product-g__buyoneclick svg,
.article-product-g__cart svg,
.article-product-g__compare svg,
.article-product-g__favorite svg {
  fill: $soft-black;
}

.article-product-g__buyoneclick:hover svg,
.article-product-g__cart:hover svg,
.article-product-g__compare:hover svg,
.article-product-g__favorite:hover svg {
  fill: $main-color;
}

.article-product-g__buyoneclick:active svg,
.article-product-g__buyoneclick:focus svg,
.article-product-g__cart:active svg,
.article-product-g__cart:focus svg,
.article-product-g__compare:active svg,
.article-product-g__compare:focus svg,
.article-product-g__favorite:active svg,
.article-product-g__favorite:focus svg {
  fill: $btn-pressed;
}
