.hr {
  position: relative;
  top: 0;

  order: 4;
  width: 100%;
  height: 2px;
  margin: 0;
  margin-top: auto;

  background-color: $border-gray;
  border: 0;

  &::after {
    content: "";
    position: absolute;
    top: 1px;

    width: 100%;
    height: 1px;

    background-color: $white;
  }
}
