@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }

  70% {
    transform: translateY(30px);
  }

  90% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
  }

  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(-10px);
  }

  20%,
  40%,
  60%,
  80% {
    transform: translateX(10px);
  }
}

.modal {
  position: fixed;
  top: 5%;
  left: calc(50% - 280px / 2);
  z-index: 1;

  display: none;
  width: 280px;
  margin: 0 auto;

  background-color: $white;
  border-top: 3px solid $main-color;
  box-shadow: 0 2px 6px transparentize($black, 0.75);

  @media (min-width: $tablet-width) {
    top: 20%;
    left: calc(50% - 460px / 2);

    width: 460px;
  }
}

.modal__show {
  display: block;

  animation: bounce 0.6s;
}

.modal__error {
  animation: shake 0.6s;
}

.modal__title {
  padding: 20px;

  font-weight: inherit;
  font-size: 24px;
  line-height: 36px;
  font-family: $font-stack-o;
  text-align: center;

  span {
    text-transform: uppercase;
  }
}

.modal__sub-title {
  padding: 20px;

  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: $font-stack-o;
  text-align: left;
}

.modal__bottom {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;

  background-color: $soft-gray;
}

.modal__button {
  width: 180px;
  padding: 10px;

  outline: none;
  cursor: pointer;

  &--change {
    background-color: $soft-black;

    &:hover,
    &:active,
    &:focus {
      background-color: $black;
    }
  }
}

.modal__button-close {
  position: absolute;
  top: 0;
  right: 0;

  padding: 0;

  background: none;
  border: none;

  &::before {
    content: "";
    position: absolute;
    top: 9px;
    right: 9px;
    z-index: 3;

    width: 14px;
    height: 14px;

    background: url("../img/icon-close.svg") no-repeat;
    background-size: contain;
  }

  &:hover {
    cursor: pointer;
  }
}

.modal__link {
  color: $main-color;

  &:hover {
    color: $btn-hover;
  }

  &:active,
  &:focus {
    color: $btn-pressed;
  }
}
