.order-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }

  @media (min-width: $desktop-width) {
    justify-content: center;
    width: 1200px;
  }
}

.order-form__field {
  width: 100%;
  margin: 0;
  margin-bottom: 20px;
  padding: 20px;

  background-color: #f7f7f7;
  border: none;

  &--recipient {
    order: 1;
  }

  &--adress {
    order: 2;
  }

  &--payments {
    order: 3;
  }

  &--delivery {
    order: 4;
  }

  @media (min-width: $tablet-width) {
    &--recipient {
      order: 1;
    }

    &--adress {
      order: 3;
    }

    &--payments {
      order: 2;
    }

    &--delivery {
      order: 4;
    }
  }

  @media (min-width: $desktop-width) {
    width: 505px;

    &:not(:nth-child(3n)):not(:last-of-type) {
      margin-right: 30px;
    }
  }
}

.order-form__title {
  font-weight: bold;
  font-size: 14px;
  font-family: $font-stack-o;
  color: $soft-black;

  @include h-default($margin: 0 0 20px 0);
}

.order-form__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include ul-default;
}

.order-form__item {
  position: relative;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 13px;

  &--email,
  &--phone {
    svg {
      position: absolute;
      top: 8px;
      right: 8px;

      fill: $soft-black;
    }
  }

  &--email:hover svg,
  &--phone:hover svg {
    fill: $black;
  }

  &--email:active svg,
  &--phone:active svg {
    fill: $green;
  }
}

.order-form__item label {
  width: 100px;

  font-weight: inherit;
  font-size: inherit;
  font-family: inherit;
  color: inherit;
}

.order-form__item input {
  position: relative;

  display: block;
  width: 58%;
  height: 34px;
  margin-right: 0;
  margin-left: auto;
  padding: 7px 16px;
  padding-right: 40px;

  font-size: 12px;
  line-height: inherit;
  font-family: inherit;
  color: inherit;

  border: 0;
  outline: none;
  box-shadow: 0 0 0 1px $border-gray;

  &:hover {
    box-shadow: 0 0 0 1px $border-gray-dark;
  }

  &:active,
  &:focus {
    box-shadow: 0 0 0 2px $soft-black;
  }

  &:active ~ svg,
  &:focus ~ svg {
    fill: $green;
  }

  &:invalid:not(:placeholder-shown),
  &:focus:invalid {
    box-shadow: 0 0 0 2px $error;
  }

  &:invalid:not(:placeholder-shown) ~ svg,
  &:focus:invalid ~ svg {
    fill: $error;
  }

  @media (min-width: $tablet-width) {
    width: 78%;
  }

  @media (min-width: $desktop-width) {
    width: 365px;
  }
}

.order-form__item input::placeholder {
  color: $soft-black;
}

.order-form__field input[type="radio"] {
  width: 0;
  margin: 0;

  opacity: 0;
}

.order-form__field input[type="radio"] ~ label {
  position: relative;

  width: 100%;
  padding-left: 18px;
}

.order-form__field label::before {
  content: "";
  position: absolute;

  width: 14px;
  height: 14px;

  transform: translate(-21px, 1px);
  opacity: 0.8;
}

.order-form__field input[type="checkbox"] ~ label::before {
  background: url("../img/icon-checkbox-off.svg") no-repeat;
  background-size: contain;
}

.order-form__field input[type="checkbox"]:checked ~ label::before {
  width: 16px;

  background: url("../img/icon-checkbox-on.svg") no-repeat;
  background-size: contain;
}

.order-form__field input[type="radio"] ~ label::before {
  background: url("../img/icon-radio-off.svg") no-repeat;
  background-size: contain;
}

.order-form__field input[type="radio"]:checked ~ label::before {
  width: 16px;

  background: url("../img/icon-radio-on.svg") no-repeat;
  background-size: contain;
}

.order-form__comment {
  display: block;
  width: 58%;
  height: 70px;
  margin-left: auto;
  padding: 13px 0 9px 14px;

  font-size: 12px;
  line-height: inherit;
  font-family: inherit;
  color: inherit;

  border: 0;
  outline: none;
  box-shadow: 0 0 0 1px $border-gray;

  &:hover {
    box-shadow: 0 0 0 1px $border-gray-dark;
  }

  &:active,
  &:focus {
    box-shadow: 0 0 0 2px $soft-black;
  }

  @media (min-width: $tablet-width) {
    width: 78%;
  }

  @media (min-width: $desktop-width) {
    width: 365px;
  }
}

.order-form__button {
  order: 5;
  align-self: center;
  width: 100%;
  height: 44px;
  margin-right: auto;
  margin-left: 0;
  padding: 10px;

  outline: none;
  cursor: pointer;

  &--clean {
    width: 150px;
    margin-top: 20px;

    color: $white;

    background-color: #454545;

    &:active,
    &:focus,
    &:hover {
      background-color: #000000;
      cursor: pointer;
    }
  }

  @media (min-width: $tablet-width) {
    margin-right: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 250px;
  }
}

.order-form__cart {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  order: 6;
  margin-left: auto;
  padding: 20px;

  border: 1px solid $border-gray;

  @media (min-width: $tablet-width) {
    margin-top: 40px;
  }

  @media (min-width: $desktop-width) {
    width: 450px;
  }
}

.order-form__cart-text {
  margin-bottom: 20px;
  padding-bottom: 10px;

  font-weight: inherit;
  font-size: inherit;
  font-family: $font-stack-r;
  color: $sub-text;

  border-bottom: 1px solid $border-gray;
}

.order-form__cart-title {
  font-weight: bold;
  font-size: 18px;
  font-family: $font-stack-o;
  color: $soft-black;

  @include h-default($margin: 0 0 20px 0);
}

.order-form__cart-price {
  font-weight: bold;
  font-size: 16px;
  font-family: $font-stack-r;
  color: $soft-black;

  @include h-default;

  &--currency {
    padding-left: 5px;

    font-size: 14px;
  }
}
