.main-list {
  line-height: 14px;
  font-family: $font-stack-o;
  text-align: right;

  @include ul-default($padding: 0 6.25% 15px);
}

.main-list__item-link {
  color: $white;

  @media (min-width: $tablet-width) {
    line-height: 21px;
    color: $soft-black;
    text-transform: uppercase;

    &--catalog {
      position: relative;

      display: block;
      width: 75px;

      &::before {
        content: "";
        position: absolute;
        top: 5px;
        left: 0;

        width: 15px;
        height: 1.5px;

        background-color: $soft-black;
        border-radius: 4px;
        box-shadow: 0 5px 0 0 $soft-black, 0 10px 0 0 $soft-black;
      }
    }

    &:hover {
      color: $main-color;

      &::before {
        background-color: $main-color;
        box-shadow: 0 5px 0 0 $main-color, 0 10px 0 0 $main-color;
      }
    }

    &--active {
      color: $main-color;

      &::before {
        background-color: $main-color;
        box-shadow: 0 5px 0 0 $main-color, 0 10px 0 0 $main-color;
      }
    }
  }
}

.main-list__item:first-child {
  padding-bottom: 8px;

  @media (min-width: $tablet-width) {
    padding: 28px 12px 28px 0;
  }

  @media (min-width: $desktop-width) {
    padding-right: 24px;
  }
}

.main-list__item:not(:first-child) {
  padding: 8px 0;

  @media (min-width: $tablet-width) {
    padding: 28px 12px;
  }

  @media (min-width: $desktop-width) {
    padding: 28px 24px;
  }
}

.main-list__item:last-child {
  position: relative;

  padding-bottom: 14px;

  border-bottom: 1px solid $black;

  &::before {
    content: "";
    position: absolute;
    top: 37px;
    left: 0;

    width: 100%;

    border-bottom: 1px solid #595959;
  }

  @media (min-width: $tablet-width) {
    padding: 28px 12px;

    border: 0;

    &::before {
      content: "";
      position: absolute;

      border: 0;
    }
  }

  @media (min-width: $desktop-width) {
    padding: 28px 24px;
  }
}
