.city-choosing {
  flex-grow: 1;

  font-size: 10px;
  font-family: $font-stack-o;
  color: $main-color;

  @media (min-width: $tablet-width) {
    font-size: 12px;
  }

  @media (min-width: $desktop-width) {
    width: 285px;

    font-size: 14px;
  }
}

.city-choosing__city {
  margin-left: 10px;

  font-size: 12px;
  font-family: inherit;
  color: $white;

  border-bottom: 1px dotted $white;

  &:hover {
    border: none;
  }

  &:active,
  &:focus {
    border: none;
    opacity: 0.8;
  }

  @media (min-width: $desktop-width) {
    font-size: 14px;
  }
}
