.page-content {
  margin: 0 20px;

  @media (min-width: $tablet-width) {
    margin: 0 40px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 1200px;
    margin: 0 auto;
  }
}

.page-content__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }

  @media (min-width: $desktop-width) {
    width: 734px;
  }
}

.page-content__title {
  position: relative;

  width: 98px;
  margin: 20px auto;

  font-weight: bold;
  font-size: 24px;
  line-height: 36px;
  font-family: $font-stack-o;
  text-align: center;
  color: $soft-black;
  text-transform: uppercase;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;

    width: 70px;
    height: 1px;

    background-color: $border-gray;
  }

  &::before {
    left: -91px;
  }

  &::after {
    left: 119px;
  }

  @media (min-width: $desktop-width) {
    width: 100%;

    &::before {
      left: 462px;
    }

    &::after {
      left: 670px;
    }
  }
}

.page-content__content-box {
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    position: relative;

    width: 330px;
    height: 230px;
    margin-bottom: 28px;
    padding: 20px;

    background-color: $white;

    &:nth-child(2n + 1) {
      margin-right: 28px;
    }

    &::after {
      content: "";
      position: absolute;
      top: 3px;
      left: 2px;
      z-index: -1;

      width: 328px;
      height: 230px;

      background-color: $main-color;
      transform: rotate(1deg);

      transition: all 0.1s linear 0s;
    }
  }
}

.page-content__content-box:hover .page-content__content-title {
  color: $main-color;
}

.page-content__content-box:hover::after {
  @media (min-width: $tablet-width) {
    top: 9px;
    left: 8px;

    transform: rotate(2deg);
  }
}

.page-content__content-box p {
  margin: 0;
}

.page-content__content-title {
  display: none;

  @media (min-width: $tablet-width) {
    display: inline-block;
    margin: 0 0 20px 0;

    font-weight: normal;
    font-size: 18px;
    font-family: $font-stack-o;
  }
}

.page-content__content a {
  color: $main-color;
}

.page-content__content a:hover {
  color: $btn-hover;
}

.page-content__content a:active,
.page-content__content a:focus {
  color: $btn-pressed;
}

.page-content__aside {
  display: none;

  @media (min-width: $desktop-width) {
    position: relative;

    display: block;
    width: 392px;
    height: 490px;
    margin-bottom: 28px;
    padding: 20px;

    background-color: $white;

    &::after {
      content: "";
      position: absolute;
      top: -1px;
      left: -2px;
      z-index: -1;

      width: 399px;
      height: 496px;

      background-color: $main-color;
    }
  }
}

.page-content__aside-list {
  @media (min-width: $desktop-width) {
    @include ul-default;
  }
}

.page-content__aside-item {
  padding-left: 49px;
}

.page-content__aside-item svg {
  position: absolute;
  left: 20px;

  width: 28px;
  height: 25px;

  fill: $main-color;
}

.page-content__aside-title {
  @media (min-width: $desktop-width) {
    font-weight: normal;
    font-size: 18px;
    font-family: $font-stack-o;
  }
}
