.main-title {
  margin: 20px 0 0 0;
  padding: 0 20px;

  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  font-family: $font-stack-o;
  color: $soft-black;

  @media (min-width: $tablet-width) {
    padding: 0 40px;

    font-size: 28px;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    margin: 20px auto;
  }
}
