.slider {
  position: relative;

  width: 280px;
  overflow-x: hidden;
  overflow-y: hidden;

  @media (min-width: $tablet-width) {
    width: 630px;
  }

  @media (min-width: $desktop-width) {
    width: 1110px;
  }
}

.slider::-webkit-scrollbar {
  display: none;
}

.slider__buttons {
  margin-bottom: 20px;

  text-align: center;
}

.slider__buttons button {
  &:hover,
  &:active,
  &:focus {
    opacity: 0.8;
  }
}

.slider__list {
  position: absolute;
  left: 0;

  display: flex;

  transition: 0.3s left ease-in-out;

  @include ul-default;
}

.slider__item {
  width: 274px;
  margin: 0 6px;
  margin-right: 20px;

  &:last-child {
    margin-right: 0;
  }

  @media (min-width: $tablet-width) {
    width: 295px;
    margin: 0 5px;
    margin-right: 25px;
  }

  @media (min-width: $desktop-width) {
    width: 255px;
    margin-right: 21px;
  }
}

.slider__before,
.slider__after {
  position: relative;

  width: 24px;
  height: 24px;
  padding: 0;

  background-repeat: no-repeat;
  background-position: center;
  border: 0;

  &::before {
    content: "";
    position: absolute;

    width: 70px;
    height: 2px;

    background-color: $border-gray;
    border-bottom: 1px solid $white;

    @media (min-width: $tablet-width) {
      width: 140px;
    }

    @media (min-width: $desktop-width) {
      width: 280px;
    }
  }

  &:hover {
    cursor: pointer;
  }

  &:focus {
    outline: none;
  }
}

.slider__before {
  background: url("../img/s-arrow-left.svg");

  &::before {
    top: 10px;
    left: -90px;

    @media (min-width: $tablet-width) {
      left: -160px;
    }

    @media (min-width: $desktop-width) {
      left: -300px;
    }
  }
}

.slider__after {
  background: url("../img/s-arrow-right.svg");

  &::before {
    top: 10px;
    right: -90px;

    @media (min-width: $tablet-width) {
      right: -160px;
    }

    @media (min-width: $desktop-width) {
      right: -300px;
    }
  }
}
