.product-social__list {
  display: flex;
  justify-content: start;

  @include ul-default;
}

.product-social__item {
  padding: 2px 0;
}

.product-social__link {
  position: relative;

  &::after {
    content: "";
    position: absolute;

    width: 14px;
    height: 14px;
  }

  &--vkontakte::after {
    width: 15px;

    background: url("../img/icon-color-vk.svg");
    background-size: cover;
  }

  &--instagram::after {
    left: 25px;

    background: url("../img/icon-color-instagram.svg");
    background-size: cover;
  }

  &--youtube::after {
    left: 50px;

    width: 21px;

    background: url("../img/icon-color-youtube.svg");
    background-size: contain;
  }
}
