.breadcrumbs {
  margin-top: 20px;
}

.breadcrumbs__list {
  display: flex;
  flex-wrap: wrap;

  @include ul-default($padding: 0 20px);

  @media (min-width: $tablet-width) {
    @include ul-default($padding: 0 40px);
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    margin: 0 auto;
  }
}

.breadcrumbs__item {
  position: relative;

  padding-right: 26px;

  font-weight: inherit;
  font-size: 12px;
  line-height: 18px;
  font-family: $font-stack-o;
  color: $soft-black;

  &:not(:last-child) {
    &::after {
      content: "";
      position: absolute;
      top: 5px;
      right: 11px;

      width: 4px;
      height: 8px;

      text-align: center;

      background: url("../img/icon-right.svg");
    }
  }
}

.breadcrumbs__item-link {
  color: $soft-black;
  text-decoration: none;

  &:hover {
    color: $btn-hover;
  }

  &:active,
  &:focus {
    color: $btn-pressed;
  }

  &:hover svg {
    fill: $btn-hover;
  }

  &:active svg,
  &:focus svg {
    fill: $btn-pressed;
  }
}
