.left-categories-list,
.left-categories-sub-list {
  @include ul-default;
}

.left-categories-list__item {
  margin: 10px 0;
}

.left-categories-list__link {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: $font-stack-o;
  color: inherit;

  &:hover {
    color: $btn-hover;
  }

  &:active,
  &:focus {
    color: $btn-pressed;
  }
}

.left-categories-sub-list {
  margin-top: 10px;
}

.left-categories-sub-list__item {
  margin: 5px 0 0 10px;
}

.left-categories-sub-list__item .left-categories-list__link {
  font-size: 12px;
  font-family: $font-stack-r;
}
