.social__list {
  display: flex;
  justify-content: start;

  @include ul-default;
}

.social__link {
  &--instagram {
    margin: 0 20px;
  }

  &--youtube {
    width: 34px;
  }

  &:hover svg {
    fill: $main-color;
  }

  &:active svg,
  &:focus svg {
    opacity: 0.5;
  }
}

.social__link svg {
  fill: $white;

  @media (min-width: $tablet-width) {
    fill: $soft-black;
  }

  @media (min-width: $desktop-width) {
    fill: $white;
  }
}
