.hero {
  position: relative;

  height: 104px;

  @media (min-width: $tablet-width) {
    height: 266px;
  }

  @media (min-width: $desktop-width) {
    height: 670px;
  }
}

.hero__container {
  position: relative;

  width: auto;

  &::before {
    content: "";
    position: absolute;
    top: 10px;
    z-index: -1;

    width: 100%;
    height: 85px;

    background-color: $white;
    background-image: url("../img/index-background-mobile@1x.jpg");
    background-repeat: no-repeat;
    background-position: top;
    background-size: contain;

    @supports (background-image: image-set(url("../img/index-background-mobile@1x.webp") 1x)) {
      background-image: image-set(url("../img/index-background-mobile@1x.webp") 1x);
    }

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/index-background-mobile@2x.jpg");

      @supports (background-image: image-set(url("../img/index-background-mobile@2x.webp") 2x)) {
        background-image: image-set(url("../img/index-background-mobile@2x.webp") 2x);
      }
    }

    @media (min-width: $tablet-width) {
      top: 0;
      left: 40%;

      width: 60%;
      height: 266px;

      background-size: 388px auto;
    }

    @media (min-width: $desktop-width) {
      top: 30px;
      left: 40px;

      width: 570px;
      height: 588px;

      background-image: url("../img/index-background-desktop@1x.jpg");
      background-position: center;
      background-size: 588px auto;

      @supports (background-image: image-set(url("../img/index-background-desktop@1x.webp") 1x)) {
        background-image: image-set(url("../img/index-background-desktop@1x.webp") 1x);
      }

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/index-background-desktop@2x.jpg");

        @supports (background-image: image-set(url("../img/index-background-desktop@2x.webp") 2x)) {
          background-image: image-set(url("../img/index-background-desktop@2x.webp") 2x);
        }
      }
    }
  }

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    height: 266px;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    height: 100%;
    margin: 0 auto;
    padding: 0 40px;
  }
}

.hero::before {
  content: "";
  position: absolute;

  width: 100%;
  height: 104px;

  background-color: transparentize($main-color, 0.2);

  @media (min-width: $tablet-width) {
    width: 40%;
    height: 266px;
  }

  @media (min-width: $desktop-width) {
    width: 90%;
    height: 670px;

    background: linear-gradient(260deg, transparent 210px, transparentize($main-color, 0.2) 211px);

    animation-name: slidein;
    animation-duration: 3s;
  }
}

.hero__header {
  @media (min-width: $tablet-width) {
    width: 40%;
  }

  @media (min-width: $desktop-width) {
    position: relative;

    height: 670px;
    padding: 160px 0;

    animation-name: slidein;
    animation-duration: 3s;
  }
}

@keyframes slidein {
  from {
    left: -100vw;
  }

  to {
    left: 0;
  }
}

.hero__button {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    width: 240px;
    height: 55px;
    margin: 30px 0 40px 40px;
    padding: 19px 0;

    font-size: 20px;
    font-family: $font-stack-o;
    text-align: center;
    color: $white;
    text-transform: uppercase;

    background-color: $soft-black;

    &:hover,
    &:active,
    &:focus {
      background-color: $black;
      cursor: pointer;
    }
  }

  @media (min-width: $desktop-width) {
    margin: 85px 0 40px 0;
  }
}

.hero__title {
  font-weight: bold;
  font-size: 18px;
  line-height: 28px;
  font-family: $font-stack-o;
  text-align: center;
  color: $white;

  @include h-default(auto, 24px);

  @media (min-width: $tablet-width) {
    width: 240px;
    margin-top: 40px;
    margin-left: 40px;
    padding: 0;

    font-size: 24px;
    line-height: 36px;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    width: 480px;
    margin: 0;

    font-size: 48px;
    line-height: 70px;
  }
}
