.category-list {
  line-height: 16px;
  font-family: $font-stack-o;
  text-align: right;

  @include ul-default($padding: 0 6.25% 15px);
}

.category-list__item,
.category-list__item a {
  color: $white;
}

.category-list__item:first-child {
  padding-bottom: 8px;
}

.category-list__item:not(:first-child) {
  padding: 8px 0;
}

.category-list__item:last-child {
  position: relative;

  padding-bottom: 14px;

  border-bottom: 1px solid $black;

  &::before {
    content: "";
    position: absolute;
    top: 39px;
    left: 0;

    width: 100%;

    border-bottom: 1px solid #595959;
  }
}

.category-list__item-link {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 190px;

    width: 5px;
    height: 8px;

    background: url("../img/left_point.svg");
  }
}
