.authorization {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 80px;
  }
}

.authorization__signin,
.authorization__register {
  width: 280px;
  margin: 0 auto 30px;

  @media (min-width: $tablet-width) {
    width: 320px;
    margin: 0;

    &:not(:nth-child(2)) {
      margin: 0 30px 30px 0;
    }
  }

  @media (min-width: $desktop-width) {
    width: 540px;
  }
}

.authorization__signin {
  margin-bottom: 20px;
}

.authorization__signin-form,
.authorization__register-form {
  padding: 20px 10px;

  @media (min-width: $tablet-width) {
    padding: 20px;
  }

  @media (min-width: $desktop-width) {
    padding: 30px;
  }
}

.authorization__register-form {
  background-color: $soft-gray;
}
