.categories {
  margin-top: 25px;

  @media (min-width: $tablet-width) {
    margin-top: 50px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    width: 1200px;
    margin: 0 auto;
    margin-top: 25px;
    margin-bottom: 55px;
    padding: 0 40px;
  }
}

.categories__main-nav-list {
  @media (min-width: $desktop-width) {
    flex-grow: inherit;
    flex-direction: column;
    width: 285px;
  }
}

.categories__button {
  display: block;
  width: auto;
  margin: 0 20px;
  padding: 12px 0;

  font-size: 20px;
  line-height: 30px;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.categories .categories__main-nav-list {
  @media (min-width: $desktop-width) {
    text-align: left;
  }
}

.categories__main-nav-list-item .categories__link {
  @media (min-width: $desktop-width) {
    color: $soft-black;

    &:hover,
    &:active {
      color: $main-color;
    }

    &:focus {
      color: $btn-pressed;
    }
  }
}

.categories__main-list {
  @media (min-width: $desktop-width) {
    width: 730px;
  }
}

.categories__main-list-title {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    margin: 0 40px 20px;

    &::before {
      left: 285px;

      width: calc((100vw - 366px));
    }
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 20px;

    &::before {
      width: 445px;
    }
  }
}

.categories__list {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }

  @media (min-width: $desktop-width) {
    @include ul-default;
  }
}

.categories .categories__main-nav-list-item {
  padding: 10px 0 10px 50px;
}

.categories .categories__main-nav-list-item:last-child {
  @media (min-width: $desktop-width) {
    border: 0;

    &::before {
      content: none;
    }
  }
}

