.blog-data {
  padding-bottom: 5px;

  border-bottom: 1px solid $border-gray;
}

.blog-data__date {
  margin-right: 20px;

  font-weight: inherit;
  font-size: 12px;
  line-height: 18px;
  font-family: $font-stack-o;
  color: $sub-text;
}

.blog-data__tag {
  font-weight: inherit;
  font-size: 12px;
  line-height: 18px;
  font-family: $font-stack-o;
  color: $main-color;
  text-transform: inherit;

  &:hover {
    color: $btn-hover;
  }

  &:active,
  &:focus {
    color: $btn-pressed;
  }
}
