.banners {
  display: none;

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 0;
    left: 50%;

    display: block;
  }
}

.banners__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include ul-default;
}

.banners__item {
  height: 180px;
  margin-left: auto;

  background: linear-gradient(80deg, transparent 31px, $soft-gray 5.2%);

  &::before {
    content: "";
    position: absolute;
    left: 100%;

    width: calc((100vw - 1218px) * 0.5);
    height: 180px;

    background-color: $soft-gray;
  }

  &:nth-child(1) {
    margin-top: 30px;

    @media (min-width: $desktop-width) {
      width: 600px;
    }
  }

  &:nth-child(2) {
    margin-top: 35px;
    margin-bottom: 35px;

    background: linear-gradient(80deg, transparent 31px, $soft-gray 5.5%);

    @media (min-width: $desktop-width) {
      width: 560px;
    }
  }

  &:nth-child(3) {
    margin-bottom: 30px;

    background: linear-gradient(80deg, transparent 31px, $soft-gray 5.9%);

    @media (min-width: $desktop-width) {
      width: 520px;
    }
  }
}

.banners__item img {
  width: 432px;
  height: 180px;
  margin-left: 32px;
}
