.manufacturer {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    width: 1200px;
    margin: 0 auto;
  }
}

.manufacturer__list {
  display: flex;
  justify-content: space-between;

  @include ul-default($padding: 0 40px);
}

.manufacturer__item {
  height: 85px;
  margin: 30px 0;
}

.manufacturer__item a {
  display: block;

  &:hover {
    border-bottom: 2px solid $main-color;
  }
}

.manufacturer__item a,
.manufacturer__item img {
  height: 85px;
}
