.search {
  position: relative;

  margin-right: 20px;

  @media (min-width: $tablet-width) {
    height: 32px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    height: 60px;
    margin-right: 15px;
    margin-left: 40px;
    padding: 9px 0;
  }
}

.search__toggle {
  z-index: 2;

  display: block;
  width: 24px;
  height: 24px;
  padding: 0;

  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    width: 24px;
    height: 24px;
  }

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.search__toggle svg {
  fill: $white;

  &:hover {
    fill: $soft-black;
  }
}

.search__form {
  position: relative;

  display: flex;
  min-width: 300px;
  height: 34px;

  vertical-align: middle;
  white-space: nowrap;

  border-top: 1px solid $black;
  border-bottom: 1px solid $black;

  &::after {
    content: "";
    position: absolute;
    top: 33px;
    left: 0;

    width: 100%;

    border-bottom: 1px solid $gray;

    @media (min-width: $desktop-width) {
      top: 41px;
    }
  }

  @media (max-width: $mobile-width-only) {
    display: none;
  }

  @media (min-width: $desktop-width) {
    height: 42px;
  }
}

.search__input[type="search"] {
  display: block;
  width: 298px;
  height: 32px;
  padding-left: 50px;

  font-size: 14px;

  border: none;
  outline: none;

  &:hover + .search__label svg {
    opacity: 1;

    fill: $soft-black;
  }

  &:active + .search__label svg,
  &:focus + .search__label svg {
    fill: $main-color;
  }

  &:active,
  &:focus {
    color: $soft-black;

    outline: none;
  }

  &::placeholder {
    color: inherit;
  }

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }

  @media (min-width: $desktop-width) {
    width: 380px;
    height: 40px;
  }
}

.search__button {
  width: 62px;
  height: 32px;

  font-size: 14px;
  line-height: 16px;
  font-family: $font-stack-r;
  text-transform: capitalize;

  border: 0;
  outline: none;
  cursor: pointer;

  @media (min-width: $desktop-width) {
    width: 80px;
    height: 40px;
  }
}

.search--opened {
  .search__box {
    @media (max-width: $mobile-width-only) {
      position: absolute;
      top: 39px;
      right: -64px;
      z-index: 1;

      width: 100vw;
      height: 75px;
      padding: 20px;

      background-color: $soft-black;
    }

    .search__form {
      display: flex;
      min-width: 100%;
    }

    &::after {
      display: none;
    }

    .search__input[type="search"] {
      width: 100%;
    }
  }

  .search__toggle svg {
    fill: $main-color;
  }
}

.search__label {
  position: absolute;
  top: 6px;
  left: 6px;
  z-index: 20;

  width: 20px;
  height: 20px;

  cursor: pointer;

  svg {
    opacity: 0.8;

    fill: $gray;
  }

  @media (min-width: $desktop-width) {
    top: 10px;
    left: 17px;
  }
}
