.top-categories-list {
  display: flex;
  flex-wrap: wrap;

  @include ul-default;
}

.top-categories-list__item {
  display: inline-block;
  margin: 0 15px 15px 0;

  background-color: $soft-gray;
}

.top-categories-list__link {
  display: inline-block;
  padding: 10px;

  font-size: 14px;
  line-height: 16px;
  font-family: $font-stack-o;
  text-align: right;
  color: $soft-black;

  &:hover {
    color: $btn-hover;
  }

  &:active,
  &:focus {
    color: $btn-pressed;
  }
}
