.sorting-filter {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
}

.sorting-filter__sort,
.sorting-filter__count-sort,
.sorting-filter__grid-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 10px;

  background-color: $white;
  border: 1px solid $border-gray;
}

.sorting-filter__sort {
  margin-right: 16px;
}

.sorting-filter__select {
  border: none;
  cursor: pointer;

  appearance: none;
}

.sorting-filter__name {
  margin: 0;
  padding-right: 10px;
}

.sorting-filter__grid-list {
  margin-left: auto;
  padding: 0;

  background: none;
  border: none;
}

.button.sorting-filter__gl-btn {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  font-size: inherit;
  font-family: inherit;
  color: $soft-black;
  text-transform: inherit;

  background: none;

  svg {
    margin-right: 5px;

    fill: $soft-black;
  }

  &--active {
    color: $main-color;

    svg {
      fill: $main-color;
    }
  }

  &:hover {
    color: $btn-hover;

    cursor: pointer;
  }

  &:active,
  &:focus {
    color: $btn-pressed;

    outline: none;
  }
}
