.bookmarks {
  height: 34px;

  @media (max-width: $mobile-width-only) {
    display: none;
  }

  @media (min-width: $tablet-width) {
    margin: 0 auto;
    padding: 8px 0;
  }

  @media (min-width: $desktop-width) {
    height: 58px;
    margin-right: 60px;
    padding: 18px 0;
  }
}

.bookmarks__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;

  @include ul-default;
}

.bookmarks__item:not(:last-of-type) {
  margin-right: 10px;

  @media (min-width: $desktop-width) {
    margin-right: 20px;
  }
}

.bookmarks__link {
  text-decoration: none;

  cursor: default;

  pointer-events: none;

  svg {
    fill: $black;
  }

  &--active {
    cursor: pointer;

    pointer-events: inherit;

    svg {
      fill: $main-color;
    }

    span {
      color: $main-color;
    }
  }

  &:hover {
    svg {
      fill: $btn-hover;
    }

    span {
      color: $btn-hover;
    }
  }

  &:active,
  &:focus {
    svg {
      fill: $btn-pressed;
    }
  }
}
