.adresses__list {
  @include ul-default($margin: 20px 0 0 0);

  @media (min-width: $tablet-width) {
    width: 50%;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
  }
}

.adresses__item {
  @media (min-width: $desktop-width) {
    width: 50%;
  }
}

.adresses__item:first-child {
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    margin-bottom: 40px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.adresses__sub-title {
  margin: 0;

  font-weight: normal;
  font-size: 20px;
  font-family: $font-stack-o;
  color: $main-color;
}

.adresses__description {
  color: $white;

  @media (min-width: $tablet-width) {
    margin-bottom: 20px;
  }
}

.adresses__phone {
  color: $main-color;

  &--number {
    font-style: italic;
    color: $white;

    border-bottom: 1px dotted white;

    &:hover,
    &:active,
    &:focus {
      opacity: 0.8;
    }
  }

  @media (min-width: $tablet-width) {
    color: $white;

    &--number {
      color: $main-color;

      border: none;

      &:hover {
        color: $btn-hover;

        opacity: 1;
      }

      &:active,
      &:focus {
        color: $btn-pressed;
      }
    }
  }
}

.adresses__citys {
  display: none;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    width: 50%;
    margin-top: 20px;
  }
}

.adresses__city-list {
  display: flex;
  flex-direction: column;

  color: $white;

  @include ul-default();

  @media (min-width: $tablet-width) {
    width: 50%;
    margin-bottom: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 25%;
  }
}

.adresses__city-item {
  @media (min-width: $tablet-width) {
    font-size: 14;
  }
}

.adresses__social {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    margin-top: 20px;
  }
}
