.special-list {
  display: flex;
  flex-direction: column;

  @include ul-default;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }

  @media (min-width: $desktop-width) {
    justify-content: flex-start;
  }
}

.special-list .special-list__item {
  width: 280px;
  margin: 0 auto 30px;

  @media (min-width: $tablet-width) {
    width: 310px;
    margin: 0 30px 30px 0;
  }

  @media (min-width: $desktop-width) {
    width: 255px;
    margin: 0;

    &:not(:nth-child(4n)) {
      margin: 0 30px 30px 0;
    }
  }

  &::before {
    content: none;
  }
}
