.single-product {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    padding: 0 40px;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    margin: 20px auto;
  }
}

.single-product__sub-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 40px;

  font-weight: inherit;
  font-size: 12px;
  line-height: 18px;
  font-family: $font-stack-o;
  color: $soft-black;

  border-bottom: 1px solid $border-gray;
}

.single-product__prod-options {
  width: 100%;
  margin-top: 20px;
  padding-top: 20px;

  border-top: 1px solid $border-gray;

  @media (min-width: $tablet-width) {
    margin-top: 40px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 20px;
  }
}

.single-product__rate,
.single-product__rewies,
.single-product__favorites,
.single-product__compare,
.single-product__social,
.single-product__article {
  margin-right: 15px;
  margin-bottom: 15px;

  @media (min-width: $tablet-width) {
    margin-bottom: 10px;
  }
}

.single-product__favorites,
.single-product__compare {
  color: $soft-black;

  &--active {
    color: $main-color;
  }

  svg {
    fill: $soft-black;
  }

  &:hover svg {
    fill: $btn-hover;
  }

  &:active svg,
  &:focus svg {
    fill: $btn-pressed;
  }

  &:hover {
    color: $btn-hover;
  }

  &:active,
  &:focus {
    color: $btn-pressed;
  }
}

.single-product__rewies {
  color: $soft-black;

  &:hover {
    color: $btn-hover;
  }

  &:active,
  &:focus {
    color: $btn-pressed;
  }
}

.single-product__favorites--active svg,
.single-product__compare--active svg {
  fill: $main-color;
}

.single-product__social {
  width: 70px;
  margin-right: 0;
}

.single-product__article {
  margin-right: 0;
  margin-left: auto;
}

.single-product__prod-image {
  width: 100%;
  margin: auto;
  margin-bottom: 40px;

  text-align: center;

  @media (min-width: $tablet-width) {
    width: 330px;
    margin: auto;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
    margin: 0;
  }
}

.single-product__prod-options-list {
  @include ul-default;
}

.single-product__prod-options-title {
  width: 100%;
  margin: 0;

  font-weight: inherit;
  font-size: 14px;
  line-height: 21px;
  font-family: $font-stack-r;
  color: $soft-black;
}

.single-product__prod-options-item {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 5px;
}

.single-product__prod-options-btn {
  display: inline-block;
  margin: 0 0;
  margin-right: 15px;
  padding: 5px 10px;

  font-weight: inherit;
  font-size: 14px;
  line-height: 16px;
  font-family: $font-stack-r;
  color: $soft-black;

  background-color: $soft-gray;
  border: 0;
  outline: none;

  &--active {
    color: $main-color;
  }

  &:hover {
    color: $btn-hover;
  }

  &:active,
  &:focus {
    color: $btn-pressed;
  }
}

.single-product__prod-options-btn:last-child {
  margin-right: auto;
}

.single-product__options-text {
  margin: 10px 0;

  font-weight: inherit;
  font-size: 14px;
  line-height: 16px;
  font-family: $font-stack-r;
  color: $soft-black;
}

.single-product__prod-cart {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  margin-top: 40px;
  padding: 30px 35px;

  background-color: $white;
  border: 1px solid $border-gray;

  @media (min-width: $desktop-width) {
    width: 550px;
    margin-top: 0;
  }
}

.single-product__prod-cart-price {
  width: 100%;
  margin: 10px 0;

  font-weight: bold;
  font-size: 26px;
  line-height: inherit;
  font-family: $font-stack-o;
  color: $soft-black;

  &--currency {
    color: $soft-black;
  }

  &--new {
    font-size: 40px;
    line-height: 36px;
    color: $green;
  }

  &--old {
    text-decoration: line-through;

    text-decoration-color: $red;
  }
}

.single-product__callback {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 0;
  padding-top: 15px;

  font-weight: inherit;
  font-size: 14px;
  line-height: inherit;
  font-family: $font-stack-o;
  color: $soft-black;

  border-top: 1px solid $border-gray;
  cursor: pointer;

  &:hover {
    color: $btn-hover;
  }

  &:active,
  &:focus {
    color: $btn-pressed;
  }
}

.single-product__callback svg {
  fill: $main-color;
}

.single-product__callback p {
  margin: 0 0 0 10px;
}

.single-product__quantity {
  display: flex;
  justify-content: space-between;
  height: 50px;
  margin-right: 15px;
}

.single-product__quantity-volume {
  padding: 18px 10px;

  background-color: $soft-gray;
  border: 1px solid $border-gray;
  cursor: pointer;

  &:hover {
    color: $main-color;
  }
}

.single-product__quantity-value {
  padding: 18px 16px;

  border-top: 1px solid $border-gray;
  border-bottom: 1px solid $border-gray;
}

.single-product__quantity,
.single-product__button-buy,
.single-product__button-buyoc {
  margin-top: 15px;
}

.single-product__button-buy,
.single-product__button-buyoc {
  flex-grow: 1;
  padding: 10px;

  font-size: 16px;
  line-height: 20px;

  outline: 0;
  cursor: pointer;
}

.single-product__button-buyoc {
  width: 100%;

  color: $soft-black;

  background-color: $soft-gray;

  &:hover {
    color: $main-color;

    background-color: $soft-gray;
  }

  &:active,
  &:focus {
    color: $btn-pressed;

    background-color: $soft-gray;
  }
}
