.page-blog {
  border-top: 1px solid $border-gray;
}

.page-blog__contacts {
  border-top: 1px solid $border-gray;
}

.page-blog__title--article {
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  font-family: $font-stack-o;
  color: $soft-black;

  @media (min-width: $desktop-width) {
    width: 1200px;
    margin: 20px auto;
    padding: 0 40px;

    font-size: 28px;
    text-align: left;
  }
}
