.products-grid {
  display: flex;
  flex-direction: column;

  @include ul-default;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }

  @media (min-width: $desktop-width) {
    justify-content: space-between;
  }
}

.products-grid__item {
  width: 280px;
  margin: 0 auto 30px auto;

  @media (min-width: $tablet-width) {
    width: 300px;
    margin: 0 30px 30px 0;
  }

  @media (min-width: $desktop-width) {
    width: 248px;
    margin: 0;

    &:not(:nth-child(3n)) {
      margin: 0 30px 30px 0;
    }
  }
}
