.filter-form__field {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;

  border: 0;
}

.filter-form__field legend {
  margin-bottom: 10px;

  font-weight: inherit;
  font-size: inherit;
  line-height: 16px;
  font-family: $font-stack-o;
  color: inherit;
}

.filter-form__range {
  position: relative;

  width: 100%;
  height: 60px;
  margin-bottom: 10px;

  background-color: $soft-gray;
}

.filter-form__toggle {
  position: relative;
  top: 50%;

  width: 180px;
  height: 3px;
  margin: 0 auto;

  background-color: $border-gray-dark;

  &--min,
  &--max {
    position: absolute;
    top: 21px;

    width: 20px;
    height: 20px;

    background-color: $white;
    border-radius: 50%;
    box-shadow: 1px 1px 4px transparentize($black, 0.75);
    cursor: pointer;
  }

  &--min {
    left: 20px;
  }

  &--max {
    right: 50px;
  }
}

.filter-form__scale {
  width: 152px;
  height: 3px;

  background-color: $main-color;
}

.filter-form__input {
  position: relative;

  display: flex;
  justify-content: space-between;
}

.filter-form__input input {
  width: 112px;
  height: 30px;

  font-weight: inherit;
  font-size: inherit;
  line-height: 16px;
  font-family: $font-stack-r;
  text-align: center;
  color: inherit;

  background-color: $soft-gray;
  border: none;
  outline-color: $main-color;
}

.filter-form__list {
  /*background-color: $soft-gray;*/
  @include ul-default($padding: 8px);
}

.filter-form__item {
  font-weight: inherit;
  font-size: inherit;
  line-height: 16px;
  font-family: $font-stack-r;
  color: inherit;
}

.filter-form__item input {
  width: 0;
  margin: 0;

  opacity: 0;
}

.filter-form__list label {
  position: relative;

  padding-left: 18px;
}

.filter-form__field label::before {
  content: "";
  position: absolute;

  width: 14px;
  height: 14px;

  transform: translate(-21px, 1px);
  opacity: 0.8;
}

.filter-form__field input[type="checkbox"] ~ label::before {
  background: url("../img/icon-checkbox-off.svg") no-repeat;
  background-size: contain;
}

.filter-form__field input[type="checkbox"]:checked ~ label::before {
  width: 16px;

  background: url("../img/icon-checkbox-on.svg") no-repeat;
  background-size: contain;
}

.filter-form__field input[type="radio"] ~ label::before {
  background: url("../img/icon-radio-off.svg") no-repeat;
  background-size: contain;
}

.filter-form__field input[type="radio"]:checked ~ label::before {
  width: 16px;

  background: url("../img/icon-radio-on.svg") no-repeat;
  background-size: contain;
}

.filter-form__field label:hover {
  cursor: pointer;

  &::before {
    opacity: 1;
  }
}

.filter-form__button {
  width: 100%;
  margin-top: 21px;
  padding: 10px;

  font-size: 16px;
  line-height: 20px;

  border: 0;
  outline: 0;
  cursor: pointer;
}
