.page-header {
  display: flex;
  flex-direction: column;

  background-color: $soft-black;
}

.page-header__wrap-top {
  position: relative;

  border-bottom: 1px solid $black;

  &::before {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;

    width: 100%;

    border-bottom: 1px solid $gray;
  }
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  width: auto;

  @media (min-width: $desktop-width) {
    width: 1200px;
    margin: 0 auto;
    padding: 10px 40px;
  }
}

.page-header__wrap-top .page-header__container {
  padding: 8px 20px 9px;

  @media (min-width: $tablet-width) {
    padding: 8px 40px 9px;
  }
}

.page-header__wrap-bottom .page-header__container {
  flex-grow: 1;
  padding: 2px 0;

  @media (min-width: $tablet-width) {
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    flex-grow: 0;
    padding: 10px 40px;
  }
}

.page-header__wrap-bottom {
  display: flex;
  justify-content: space-between;
  height: 49px;
  padding: 8px 20px 9px;

  border-bottom: 1px solid $black;

  @media (min-width: $tablet-width) {
    position: relative;

    height: 130px;
    padding: 8px 40px 9px;

    border-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    position: relative;

    height: 160px;
    padding: 0;

    border-bottom: 0;
  }
}

.page-header__phone {
  font-size: 10px;
  font-family: $font-stack-o;
  color: $main-color;

  span {
    display: none;
  }

  &--number {
    margin-left: 10px;

    font-size: 12px;
    font-family: inherit;
    color: $white;

    border-bottom: 1px dotted $white;

    &:hover {
      border: none;
    }

    &:active,
    &:focus {
      border: none;
      opacity: 0.8;
    }

    @media (min-width: $desktop-width) {
      font-size: 14px;
    }
  }

  @media (min-width: $tablet-width) {
    margin-right: 30px;

    font-size: 12px;

    span {
      display: inline;
    }
  }

  @media (min-width: $desktop-width) {
    width: 280px;
    margin: 0;

    font-size: 14px;
  }
}

.page-header__payment {
  position: relative;

  display: none;

  font-size: 12px;
  font-family: $font-stack-o;
  color: white;

  border-bottom: 1px dotted white;

  &::before {
    content: "";
    position: absolute;
    top: calc(50% / 2 - 2px);
    left: -24px;

    width: 14px;
    height: 12px;

    background: url("../img/credit-card.svg");
  }

  &:hover {
    border: none;
  }

  &:active,
  &:focus {
    border: none;
    opacity: 0.8;
  }

  @media (min-width: $tablet-width) {
    display: inline;
    margin: 0 32px 0 24px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 34px;

    font-size: 14px;
  }
}

.page-header__delivery {
  position: relative;

  display: none;

  font-size: 12px;
  font-family: $font-stack-o;
  color: white;

  border-bottom: 1px dotted white;

  &::before {
    content: "";
    position: absolute;
    top: calc(50% / 2 - 2px);
    left: -24px;

    width: 14px;
    height: 12px;

    background: url("../img/truck.svg");
  }

  &:hover {
    border: none;
  }

  &:active,
  &:focus {
    border: none;
    opacity: 0.8;
  }

  @media (min-width: $tablet-width) {
    display: inline;
    margin-left: 24px;
  }

  @media (min-width: $desktop-width) {
    font-size: 14px;
  }
}

.page-header__office-hours {
  display: none;
  height: 58px;
  padding: 6px 0;

  font-size: 12px;
  color: $white;

  p {
    margin: 0;
  }

  span {
    font-size: 12px;
    font-family: $font-stack-r;
    color: $main-color;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: column;
    margin-right: auto;
  }
}

.page-header__cart {
  height: 34px;
  padding: 8px 0;

  font-size: 14px;
  font-family: $font-stack-o;

  @media (max-width: $mobile-width-only) {
    display: none;
  }

  @media (min-width: $tablet-width) {
    padding-left: 0;
  }

  @media (min-width: $desktop-width) {
    height: 64px;
    padding: 18px 0;
  }
}

.page-header__cart-link {
  position: relative;

  font-size: inherit;
  font-family: inherit;
  color: white;

  svg {
    fill: $main-color;
  }

  span {
    margin-right: 10px;

    color: $main-color;
  }

  &:hover {
    svg {
      fill: $btn-hover;
    }

    span {
      color: $btn-hover;
    }
  }

  &:active,
  &:focus {
    svg {
      fill: $btn-pressed;
    }

    span {
      color: $btn-pressed;
    }
  }

  @media (min-width: $tablet-width) {
    display: inline;
  }
}

.page-header__phone-main,
.page-header__mail-main {
  order: 5;

  &--target {
    margin-left: 10px;

    font-weight: bold;
    font-size: 20px;
    color: $main-color;
    text-transform: uppercase;

    &:hover,
    &:active,
    &:focus {
      opacity: 0.8;
    }
  }

  @media (min-width: $tablet-width) {
    padding: 28px 0;

    font-family: $font-stack-o;
    color: $soft-black;
  }
}

.page-header__phone-main {
  display: none;

  @media (min-width: $tablet-width) {
    display: inline-block;
  }
}

.page-header__mail-main {
  display: none;

  @media (min-width: $desktop-width) {
    display: inline-block;
    margin-left: 20px;
  }
}

.page-header__logo {
  height: 34px;

  @media (min-width: $desktop-width) {
    height: 61px;
  }
}

.page-header__logo-image {
  @media (min-width: $tablet-width) {
    display: block;
    margin-top: 3px;
  }

  @media (min-width: $desktop-width) {
    width: 220px;
    margin: 0;
  }
}

.page-header__user-list {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    flex-grow: 1;

    @include ul-default();
  }

  @media (min-width: $desktop-width) {
    width: 370px;
  }
}
