.viewed-product h2 {
  margin: 20px 6.25% 16px;

  font-weight: normal;
  font-size: 24px;
  line-height: 36px;
  font-family: $font-stack-o;
  text-align: center;
  text-transform: uppercase;
}

.viewed-product__slider {
  height: 480px;
  margin: 0 auto 20px;

  @media (min-width: $tablet-width) {
    height: 510px;
    margin: 0 auto;
  }
}
