.products {
  margin: 25px auto 0;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    padding: 0 40px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    width: 1200px;
  }
}

.products__aside {
  display: none;

  @media (min-width: $tablet-width-only) {
    display: flex;
    flex-direction: column;
    width: 285px;
  }
}

.products__aside-list-title {
  @media (min-width: $desktop-width) {
    &::before {
      left: 110px;

      width: 175px;
    }
  }
}

.products__main {
  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: column;
    width: 805px;
  }
}

.products__sorting-bar {
  margin: 15px 0 30px;
  padding: 10px;

  background-color: #f7f7f7;
}

.products__pagination {
  margin: 30px 0;
}

.products__content {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    margin-bottom: 30px;
  }
}
