.our-partners {
  padding-bottom: 20px;

  background-color: $soft-gray;
  background-image: url("../img/bg-blink-gray.png");
  background-repeat: no-repeat;
  background-size: cover;
  border-top: 1px solid $border-gray;

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.our-partners__title {
  margin: 20px 20px 16px;

  font-weight: normal;
  font-size: 24px;
  line-height: 36px;
  font-family: $font-stack-o;
  text-align: center;
  text-transform: uppercase;
}

.our-partners__slider {
  height: 158px;
  margin: 0 auto 20px;

  @media (min-width: $tablet-width) {
    height: 230px;
    margin: 0 auto;
  }
}
