.product-cart {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 40px auto;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0 40px;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    margin: 80px auto;
  }
}

.product-cart__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include ul-default($margin: 0 0 40px 0);

  @media (max-width: $tablet-width-only) {
    width: 100%;
  }

  @media (min-width: $desktop-width) {
    width: 640px;
  }
}

.product-cart__item {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  text-align: center;

  &:not(:last-child) {
    margin-bottom: 5px;
    padding-bottom: 10px;

    border-bottom: 1px solid $border-gray;
  }
}

.product-cart__description {
  align-self: start;
  margin: 0;
  margin-right: auto;
}

.product-cart__brand,
.product-cart__options {
  font-weight: inherit;
  font-size: 10px;
  line-height: 12px;
  font-family: inherit;
  text-align: left;
  color: $sub-text;

  @include h-default;
}

.product-cart__button {
  display: block;
  margin-top: 5px;
  padding: 0;

  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
  text-align: left;
  color: $main-color;
  text-transform: inherit;

  background: none;
  outline: none;
  cursor: pointer;

  &:hover {
    color: $btn-hover;

    background: none;
  }

  &:active,
  &:focus {
    color: $btn-pressed;

    background: none;
  }
}

.product-cart__title {
  font-weight: inherit;
  font-size: inherit;
  font-family: $font-stack-o;
  color: $soft-black;
}

.product-cart__price {
  width: 100px;
  margin-right: 10px;

  font-weight: bold;
  font-size: 16px;
  font-family: $font-stack-r;
  text-align: left;
  color: $soft-black;

  @include h-default;

  &--currency {
    padding-left: 5px;

    font-size: 14px;
  }
}

.product-cart__quantity {
  display: flex;
  justify-content: space-between;
  width: 95px;
  height: 50px;
  margin-right: 15px;
}

.product-cart__quantity-volume {
  padding: 18px 10px;

  background-color: $soft-gray;
  border: 1px solid $border-gray;
  cursor: pointer;

  &:hover {
    color: $main-color;
  }
}

.product-cart__quantity-value {
  padding: 18px 16px;

  border-top: 1px solid $border-gray;
  border-bottom: 1px solid $border-gray;
}

.product-cart__cart {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 20px;

  border: 1px solid $border-gray;

  @media (min-width: $desktop-width) {
    width: 450px;
  }
}

.product-cart__cart-link {
  width: 100%;
  height: 40px;

  line-height: 40px;
}

.product-cart__cart-text {
  margin-bottom: 20px;
  padding-bottom: 10px;

  font-weight: inherit;
  font-size: inherit;
  font-family: $font-stack-r;
  color: $sub-text;

  border-bottom: 1px solid $border-gray;
}

.product-cart__cart-title {
  font-weight: bold;
  font-size: 18px;
  font-family: $font-stack-o;
  color: $soft-black;
}

.product-cart__cart-price {
  font-weight: bold;
  font-size: 16px;
  font-family: $font-stack-r;
  color: $soft-black;

  &--currency {
    padding-left: 5px;

    font-size: 14px;
  }
}
